.app{
    display: flex;
    flex-direction: row;
}
.main{
    width: 1100px;
    height: 970px;
    border: 2px solid white;
    margin-top: 70px;
    border-radius:2%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background-color: white;
}
.mainCat{
    width: 1100px;
    height: 970px;
    border: 2px solid white;
    margin-top: 70px;
    border-radius:2%;
    display: flex;
    flex-direction: row;
    background-color: white;
    justify-content: space-between;
}
.category{
    width: 500px;
    margin: 25px;
}
.categoryAdd{
    display: flex;
    flex-direction: column;
}
.generalCat{
    background-color: #e5e4e4;
    margin-top: 15px;
    height: 60px;
    cursor: pointer;
    font-size: 17px;
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #e7e6e6;
    align-items: center;
    padding-left: 10px;
}
.cat{
    padding-left: 10px;
    height: 60px;
    cursor: pointer;
    font-size: 17px;
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #e7e6e6;
    align-items: center;
}