.navbar{
    display: flex;
    flex-direction: row;
    margin: 50px 0px 0 100px;
font-family: "Times New Roman",sans-serif;
    font-size: 25px;
    text-decoration: none;
    color: black;
}
.link{ color: black;
    width: 220px;
    height: 60px;
    margin-top: 12px;
    border-radius:5%;
    background-color: #efefef;
    display: flex;
    flex-direction: row;
    cursor: pointer;
    text-decoration: none;
}
.link__active{
    width: 220px;
    height: 60px;
    margin-top: 12px;
    border-radius:5%;
    background-color: rgb(209, 34, 227);
    display: flex;
    flex-direction: row;
    cursor: pointer;
    text-decoration: none;
}
.navbar__box{
    width: 180px;
    display: flex;
    flex-direction: column;
    margin: 50px;
}
.icon{
    margin-right: 7px;
}
.linkTo{
    text-decoration: none;
    color: black;
}
