.app{
    display: flex;
    flex-direction: row;
}
.main{

    width: 1000px;
    height: 970px;
    border: 2px solid white;
    margin-top: 70px;
    border-radius:2%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background-color: white;
}
.inpLog{
margin-right:10px;
    margin-left:10px;
    width:33.33%
}
.generalBrands{
    background-color: #e5e4e4;
    height: 50px;
    font-size: 17px;
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #e7e6e6;
    align-items: center;
    padding-left: 10px;
    flex-wrap: wrap;
    gap: 15%;
}
.mainBrands{
    margin: 20px;

}

.brands{
    padding-left: 10px;
    height: 150px;
    font-size: 17px;
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #e7e6e6;
    align-items: center;
    justify-content: space-between;
}
.imges{
    display: flex;
    flex-direction: row;
}
img{
    margin-right: 15px;
}