.app{
    display: flex;
    flex-direction: row;
}
.main{

    width: 1000px;
    height: 970px;
    border: 2px solid white;
    margin-top: 70px;
    border-radius:2%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background-color: white;
}
.mainCat{
    width: 1100px;
    height: 970px;
    border: 2px solid white;
    margin-top: 70px;
    border-radius:2%;
    display: flex;
    flex-direction: row;
    background-color: white;
    justify-content: space-between;
}
.banners{
    padding-left: 10px;
    height: 80px;
    cursor: pointer;
    font-size: 17px;
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #e7e6e6;
    align-items: center;
    justify-content: space-between;
}
.mainBanners{
    background-color: #e5e4e4;
    height: 50px;
    cursor: pointer;
    font-size: 17px;
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #e7e6e6;
    align-items: center;
    padding-left: 10px;
    margin-top: 10px;
}
.generalBan{
    margin: 20px;
}