.app{
    display: flex;
    flex-direction: row;
}
.main{

    width: 1000px;
    height: 970px;
    border: 2px solid white;
    margin-top: 70px;
    border-radius:2%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background-color: white;
}

.generalCities{
    padding: 15px;
    background-color: #e5e4e4;
    font-size: 21px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 42%;
}

.cities{
    margin: 20px;
    font-size: 17px;
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #e7e6e6;
justify-content: space-between;
}
.citiesAdd{
    display: flex;
    flex-direction: row;
margin: 20px;
}
.address{
  margin-left: 250px;
    overflow: hidden;
    text-overflow: ellipsis;
}