.app{
    display: flex;
    flex-direction: row;
}
.main{
    width: 1000px;
    height: 970px;
    border: 2px solid white;
    margin-top: 70px;
    border-radius:2%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background-color: white;
}
.clients{
    margin: 25px;
    font-size: 17px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-bottom: 1px solid #e7e6e6;
}
.clMap{
    overflow: scroll;
    height: 770px;
}
